body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  background: #d0b4dc; /* Set the background color for the entire page */
}

.color-picker {
  background: #c8b4dc;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.color-picker button {
  background: #ffc4cc;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.color-options {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  gap: 10px;
  margin-top: 20px;
}

.color-option {
  width: 50px;
  height: 50px;
  border: 2px solid #fff;
  border-radius: 5px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.selected-color {
  margin-top: 20px;
  font-size: 20px;
}

.color-swatch {
  width: 50px;
  height: 50px;
  border: 2px solid #fff;
  border-radius: 5px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}

.color-option:hover {
  transform: scale(1.1);
}
